div {
    display: block;
}

#drum-machine {
  text-align: center;
}
.inner-container {
    outline: 5px solid orange;
    position: relative;
    width: 660px;
    text-align: center;
    background-color: #b3b3b3;
}

.inner-container .pad-bank {
    width: 332px;
    height: 272px;
    display: inline-block;
    margin: 20px;
    /* float: left; */
}

.inner-container .pad-bank .drum-pad {
    position: relative;
    float: left;
    width: 100px;
    height: 80px;
    margin-right: 10px;
    border-radius: 5px;
    padding-top: 35px;
    box-sizing: border-box;
    cursor: pointer;
    background-color: grey;
    margin-top: 10px;
    box-shadow: black 3px 3px 5px;
}

.inner-container .logo {
    position: absolute;
    top: 5px;
    right: 10px;
}
.inner-container .logo img {
  width: 70px;
}
.inner-container .controls-container {
    width: 240px;
    height: 272px;
    display: inline-block;
    margin: 40px 20px 0 10px;
    vertical-align: top;
}

.inner-container .controls-container .control .select {
    margin: auto;
    border: 1px solid black;
    width: 50px;
    height: 20px;
    padding: 1px;
    background-color: black;
}

.inner-select, .inner-container .controls-container .control .select .inner {
    width: 23px;
    height: 19px;
    background: blue;
    border: 1px solid black;
    box-sizing: border-box;
    cursor: pointer;
}

.inner-container .controls-container #display {
    width: 200px;
    background-color: gray;
    margin: 15px auto;
    padding: 15px;
    box-sizing: border-box;
}

[type='range'] {
    margin: 12.5px 0;
    width: 100%;
}
input[type="range" i] {
    appearance: auto;
    cursor: default;
    color: internal-light-dark(rgb(16, 16, 16), rgb(255, 255, 255));
    padding: initial;
    border: initial;
    margin: 2px;
}

div[disabled]
{
  pointer-events: none;
  opacity: 0.7;
}